body {
  margin: 0;
  display: block;
}

.stars {
  display: flex;
  align-items: center;
  padding: 0;

}

.stars__star {
  background: url(./images/star.svg) no-repeat center;
  height: 16px;
  width: 16px;
  margin: 0 2px;
}

.stars--1 :first-child,
.stars--2 :nth-child(-n + 2),
.stars--3 :nth-child(-n + 3),
.stars--4 :nth-child(-n + 4),
.stars--5 :nth-child(-n + 5) {
  background: url(./images/star-active.svg) no-repeat center;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars__star:last-child {
  margin-right: 0;
}
